@import url(https://fonts.googleapis.com/css2?family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Old Standard TT', serif;
}

@media screen and (max-width: 768px) {
  * {
    background-color: #FFE9C6;
  }
  
}
@media screen and (max-width: 768px) {
  #inputs-radio-tasks {
    padding: 15px;
    background-color: aliceblue !important;  

  }
  .input-label {
    padding: 5px;
    font-size: 1.1rem;
    background-color: aliceblue !important;  

  }
  .task-input-text-container {
    display: flex;
    justify-content: center;
    background-color: aliceblue !important;  

  }
  .task-text-input {
    height: 35px;
    width: 230px;
    font-size: 1.1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
  }

  .task-save-button {
    padding: 8px;
    margin-left: 15px;
    font-size: 1.1rem;
  }

  #other-text-input {
    height: 25px;
    width: 200px;
    font-size: 1.1rem;
    padding: 5px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;  

  }

  #other-save-button {
    padding: 8px;
    margin-left: 15px;
    font-size: 1.1rem;
    background-color: #A1B5FF;
    border: none;
    border-radius: 5px;
  }

  .other-save {
    margin-top: 10px;
    padding: 5px;
    border-radius: 3px;
    margin-right: 10px;
    transition: 0.7s;
    font-size: 1.1rem;
    border: none;
    background-color: aliceblue !important;
  }

  .task-save-button {
    padding: 8px;
    margin-left: 15px;
    font-size: 1.1rem;
    background-color: #A1B5FF;
    border: none;
    border-radius: 5px;
  }

  .task-input {
    border: 1px solid #ccc;
    text-align: center;
    padding: 10px;
    border-radius: 8px;
    background-color: aliceblue !important;  
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 768px) {
  .task-list-container {
    width: 80%;
    padding: 15px;
    font-size: 1.3rem;
    border: 1px solid #252526;
    border-radius: 3px;
    margin-bottom: 15px;
    box-shadow: 5px 10px #888888;
    background-color: aliceblue !important;
  }
  .done-task {
    text-decoration: line-through;
    background-color: #F2B5A7;
    transition: 0.7s;
    width: 90%;
    list-style-type: none;
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 5px;
    color: #252526;
  }
  
  .not-done-task {
    text-decoration: none;
    background-color: #92E887;
    transition: 0.7s;
    width: 90%;
    list-style-type: none;
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 5px;
    color: #252526;
  }

  .btn-task-list {
    padding: 5px;
    border-radius: 3px;
    margin-right: 10px;
    transition: 0.7s;
    font-size: 1.1rem;
    border: none;
    width: 80px;
  }

  .btn-task-list:hover {
    border: 2px solid #252526;
  }

  #false-btn {
    background-color: #A1B5FF;
    color: #252526;   
  }

  #true-btn {
    background-color: #FFD094;
    color: #252526;
  }

  #edit-btn {
    background-color: #FFF69E;
    color: #252526;
  }

  #delete-btn {
    background-color: #EBAAA7;
    color: #252526;   
  }

  #extra-edit-input {
    height: 25px;
    width: 200px;
    font-size: 1.1rem;
    padding: 5px;
    background-color: #fff;
  }

  .extra-edit-container {
    padding: 20px !important;
    background-color: aliceblue !important;
  }

  #extra-edit-btn {
    margin-top: 10px;
    padding: 5px;
    border-radius: 3px;
    margin-right: 10px;
    transition: 0.7s;
    font-size: 1.1rem;
    border: none;
    width: 60px;
    background-color: #888888;
  }
}

@media screen and (max-width: 768px) {
  #filter-label {
    font-size: 1.2rem;
    padding: 5px;
    background-color: aliceblue !important;
  }
  .task-filter-container {
    border: 1px solid #ccc;
    text-align: center;
    padding-bottom: 20px;
    border-radius: 8px;
    background-color: aliceblue !important;
  }

  .task-filter-container h3 {
    background-color: aliceblue !important;
  }

}
@media screen and (max-width: 768px) {
  .task-big-container {
    width: 90vw;
    margin: 0 auto;
  }
  
}
@media screen and (max-width: 768px) {
  #header-title {
    font-size: 2.5rem;
    text-align: center;
  }
  
}
